  line-height: var(--pf-global--FontSize--xl); }
  .co-quick-start-task-review__actions {
    display: flex;
    align-items: flex-start;
    margin-bottom: var(--pf-global--spacer--sm); }
    .co-quick-start-task-review__actions input[type='radio'] {
      margin-top: 0;
      margin-right: 0; }
  .co-quick-start-task-review__radio {
    margin-right: var(--pf-global--spacer--xl) !important; }
  .co-quick-start-task-review--success {
    color: var(--pf-global--palette--green-500); }
  .co-quick-start-task-review--failed {
    color: var(--pf-chart-global--danger--Color--100); }

.ocs-modal {
  position: absolute !important; }

@keyframes ocs-spotlight-expand {
  0% {
    outline-offset: -4px;
    outline-width: 4px;
    opacity: 1; }
  100% {
    outline-offset: 21px;
    outline-width: 12px;
    opacity: 0; } }

@keyframes ocs-spotlight-fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes ocs-spotlight-fade-out {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.ocs-spotlight {
  pointer-events: none;
  position: absolute; }
  .ocs-spotlight__with-backdrop {
    mix-blend-mode: hard-light; }
  .ocs-spotlight__element-highlight-noanimate {
    border: var(--pf-global--BorderWidth--xl) solid var(--pf-global--palette--blue-200);
    background-color: var(--pf-global--palette--black-500);
    z-index: 9999; }
  .ocs-spotlight__element-highlight-animate {
    pointer-events: none;
    position: absolute;
    box-shadow: inset 0px 0px 0px 4px var(--pf-global--palette--blue-200);
    opacity: 0;
    animation: 0.4s ocs-spotlight-fade-in 0s ease-in-out, 5s ocs-spotlight-fade-out 12.8s ease-in-out;
    animation-fill-mode: forwards; }
    .ocs-spotlight__element-highlight-animate::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      animation: 1.2s ocs-spotlight-expand 1.6s ease-out;
      animation-fill-mode: forwards;
      outline: 4px solid var(--pf-global--palette--blue-200);
      outline-offset: -4px; }
